.products {
  text-align: center;
  padding: 40px 20px;
  background-color: #f9f9f9;
}

.products__title {
  font-size: 32px;
  color: #E39746;
  margin-bottom: 20px;
  font-weight: bold;
}

.products__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.products__list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.products__item {
  background-color: #FFFFFF;
  border: 2px solid #86B8B3;
  border-radius: 12px;
  padding: 20px;
  width: 280px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.products__item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15);
}

.products__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.products__title {
  font-size: 20px;
  color: #E39746;
  font-weight: bold;
  margin: 0;
}

.products__price {
  font-size: 18px;
  color: #86B8B3;
  font-weight: bold;
  background-color: rgba(134, 184, 179, 0.15);
  padding: 5px 10px;
  border-radius: 8px;
}

.products__desc {
  font-size: 14px;
  color: #555;
  line-height: 1.5;
  margin-top: 10px;
}
