.products {
  text-align: center;
  background-color: #f9f9f9;
  padding: 40px 20px;
}

.products__title {
  color: #e39746;
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: bold;
}

.products__box {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.products__list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.products__item {
  background-color: #fff;
  border: 2px solid #86b8b3;
  border-radius: 12px;
  width: 280px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 10px #0000001a;
}

.products__item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px #00000026;
}

.products__wrapper {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.products__title {
  color: #e39746;
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.products__price {
  color: #86b8b3;
  background-color: #86b8b326;
  border-radius: 8px;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: bold;
}

.products__desc {
  color: #555;
  margin-top: 10px;
  font-size: 14px;
  line-height: 1.5;
}
